import React from 'react'
import { Text, createStyles } from '@instacart/cocktail'
import { useConfig, useLang } from '@app/services/config/hooks'
import InstacartLogo from '@app/static/images/ic-express/instacart-logo-30px.png'

interface ExpressPoweredByProps {
  small?: boolean
  poweredByText?: string
}

const useStyles = createStyles(tokens => ({
  instacartLogo: {
    paddingLeft: `${tokens.size.spacing.xxSmall}px`,
  },
  instacartLogoSmall: {
    height: `18px`,
    paddingLeft: `${tokens.size.spacing.xxxSmall}px`,
  },
  poweredBy: {
    alignItems: 'center',
    display: 'flex',
  },
}))

const ExpressPoweredBy = ({ small, poweredByText }: ExpressPoweredByProps) => {
  const lang = useLang()
  const { icExpress } = useConfig()

  const styles = useStyles()

  return (
    <div css={styles.poweredBy}>
      <Text component="div" variant={small ? 'body' : 'large'} color="textSecondary" bold>
        {poweredByText || icExpress.poweredByText}
      </Text>
      <img
        alt={lang.instacart.instacart}
        css={small ? styles.instacartLogoSmall : styles.instacartLogo}
        src={InstacartLogo}
      />
    </div>
  )
}

export default ExpressPoweredBy
